.Modal {
  max-width: 80%;
  width: 100%;
  background: white;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  z-index: 1000;
  margin: 0 auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 10vh;
  height: 80vh;
}
.content-modal {
  position: fixed;
  left: 0;
  right: 0;
  background: #000000a1;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 100;
}

.body-modal {
  overflow-y: scroll;
  height: 100%;
}

.Modal-close {
  width: 50px;
  height: 50px;
  position: absolute;
  right: -25px;
  top: -25px;
  border-radius: 50%;
  border: none;
  background: #33afb3;
  color: white;
  cursor: pointer;
}

.Modal-close:after {
  content: "X";
  font-weight: bold;
  font-size: 30px;
}

.body-modal::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

.body-modal::-webkit-scrollbar {
  width: 9px;
  border-radius: 10px;
  background-color: #f5f5f5;
}

.body-modal::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: transparent
    linear-gradient(
      180deg,
      rgba(49, 175, 180, 1) 0%,
      rgba(17, 85, 113, 1) 50%,
      rgba(20, 33, 45, 1) 100%
    ) !important;
}

